<template>
<div class="advanSetInfo">
  <div class="wzborder"><span class="wzborder_mtitle"></span>
    <p><b class="infoname">MUX</b><input class="infoinput_checkbox" type="checkbox" name="mux" v-model="outBset.mux"><b class="infoname infoname_long">concurrency</b><input name="concurrency" v-model="outBset.concurrency" placeholder="1-1024" class="infoinput infoinput_two"></p>
    <p><b class="infoname infoname_long">tcpfastopen</b><input class="infoinput_checkbox" type="checkbox" name="tcpfastopen" v-model="outBset.tcpfastopen"><b class="infoname">TLS</b><select name="tls" v-model="outBset.tls" class="infoinput infoinput_two">
        <option>none</option>
        <option>tls</option>
      </select></p>
    <p><b class="infoname infoname_long">allowInsecure</b><input class="infoinput_checkbox" type="checkbox" name="allowInsecure" v-model="outBset.allowInsecure"></p>
    <p><b class="infoname infoname_long">domainStrategy</b><select name="domainStrategy" v-model="outBset.domainStrategy" class="infoinput infoinput_middle">
        <option>AsIs</option>
        <option>IPIfNonMatch</option>
        <option>IPOnDemand</option>
      </select></p>
  </div>
  <div class="wzborder csoutbset"><span class="wzborder_mtitle"></span>
    <p class="center"></p>
    <p class="csoutborder"><b class="infoname infoname_short"></b><select v-model="outBset.outborder[0]" class="infoinput infoinput_two">
        <option value="proxy"></option>
        <option value="direct"></option>
        <option value="block"></option>
      </select><select v-model="outBset.outborder[1]" class="infoinput infoinput_two">
        <option value="proxy"></option>
        <option value="direct"></option>
        <option value="block"></option>
      </select></p>
    <p class="tip">* <br>*   -   - </p>
    <p class="csroutorder"><b class="infoname infoname_short"></b><select v-model="outBset.routorder[0]" class="infoinput infoinput_two">
        <option value="proxy"></option>
        <option value="direct"></option>
        <option value="block"></option>
      </select><select v-model="outBset.routorder[1]" class="infoinput infoinput_two">
        <option value="proxy"></option>
        <option value="direct"></option>
        <option value="block"></option>
      </select></p>
  </div>
  <div class="wzborder cskcpset"><span class="wzborder_mtitle">KCP</span>
    <p><b class="infoname">mtu</b><input name='mtu' v-model='outBset.kcp[0]' placeholder=' 576 - 1460' class="infoinput"></p>
    <p><b class="infoname">tti</b><input name='tti' v-model='outBset.kcp[1]' placeholder=' 10 - 100' class="infoinput"></p>
    <p><b class="infoname infoname_long">ulinkCapacity</b><input name='uplinkCapacity' v-model='outBset.kcp[2]' placeholder=' MB/s' class="infoinput infoinput_middle"></p>
    <p><b class="infoname infoname_long">dlinkCapacity</b><input name='downlinkCapacity' v-model='outBset.kcp[3]' placeholder=' MB/s' class="infoinput infoinput_middle"></p>
    <p><b class="infoname infoname_long">congestion</b><input class="infoinput_checkbox" type="checkbox" name="congestion" v-model="outBset.kcp[4]"><b class="infoname"></b><select v-model="outBset.kcp[7]" name="type" class="infoinput infoinput_two">
        <option>none</option>
        <option>srtp</option>
        <option>utp</option>
        <option>wechat-video</option>
        <option>dtls</option>
        <option>wireguard</option>
      </select></p>
    <p><b class="infoname">rBSize</b><input name='readBufferSize' v-model='outBset.kcp[5]' placeholder=' MB' class="infoinput infoinput_two"><b class="infoname">wBSize</b><input name='writeBufferSize' v-model='outBset.kcp[6]' placeholder=' MB' class="infoinput infoinput_two"></p>
  </div>
  <p class="center"><button @click="saveadvanSet" class="nbutton nbutton--long"></button></p>
</div>
</template>

<script>
export default {
  name: 'elecV2SetAdvan',
  data(){
    return {
      outBset: this.$store.state.gConf.outBset
    }
  },
  methods: {
    saveadvanSet(){
      this.outBset.concurrency = Number(this.outBset.concurrency)
      if ((this.outBset.outborder[0] == this.outBset.outborder[1]) || (this.outBset.routorder[0] == this.outBset.routorder[1])) {
        this.$elecV2Alert("")
        return
      }
      for (let v of ["direct", "block", "proxy"]) {
        if (this.outBset.outborder.indexOf(v) == -1) this.outBset.outborder[2] = v
        if (this.outBset.routorder.indexOf(v) == -1) this.outBset.routorder[2] = v
      }
      this.outBset.kcp[0] = Number(this.outBset.kcp[0]) || 1350
      this.outBset.kcp[1] = Number(this.outBset.kcp[1]) || 50
      this.outBset.kcp[2] = Number(this.outBset.kcp[2]) || 5
      this.outBset.kcp[3] = Number(this.outBset.kcp[3]) || 20
      this.outBset.kcp[5] = Number(this.outBset.kcp[5]) || 2
      this.outBset.kcp[6] = Number(this.outBset.kcp[6]) || 2

      this.$store.commit('saveGconf')
      this.$elecV2Alert("")
    }
  }
}
</script>

<style scoped>
.csoutbset .infoinput {
  margin-left: 36px;
}
</style>