<template>
  <div>
    <div class="wzborder"><span class="wzborder_mtitle"></span>
      <p><b class="infoname infoname_long">socks </b><input class="infoinput infoinput_middle" name='sport' v-model='inbound.sport'></p>
      <p><b class="infoname infoname_long">HTTP </b><input class="infoinput infoinput_middle" name='hport' v-model='inbound.hport'></p>
      <p class="center"><i>*</i></p>
    </div>
    <div class='wzborder'><span class="wzborder_mtitle">DNS</span>
      <p><b class="infoname">DNS</b><textarea name='dns' placeholder='8.8.8.8,1.1.1.1' v-model="dns" class="infoinput infoinput_textarea"></textarea></p>
    </div>
    <div class="wzborder"><span class="wzborder_mtitle">log </span>
      <p><b class="infoname infoname_long"></b><input class="infoinput infoinput_middle" name="access" placeholder="" v-model="log.access"></p>
      <p><b class="infoname infoname_long"></b><input class="infoinput infoinput_middle" name="error" placeholder="" v-model="log.error"></p>
      <p><b class="infoname infoname_long"></b><select v-model="log.loglevel" name="loglevel" class="infoinput infoinput_middle">
          <option>debug</option>
          <option>info</option>
          <option>warning</option>
          <option>error</option>
          <option>none</option>
        </select></p>
      <p class="center"><i>* stdout<br>* none</i></p>
    </div>
    <div class="wzborder"><span class="wzborder_mtitle"></span>
      <p><b class="infoname infoname_long"></b><input class="infoinput infoinput_middle" name="maxSubsers" v-model="evset.maxSubsers"></p>
      <p><input type="checkbox" class="infoinput_checkbox" name="isRandsub" v-model="evset.isRandsub"><span></span>
        <p class="tip">* </p>
    </div>
    <div class="wzborder"><span class="wzborder_mtitle"></span>
      <p><input name="isLocalStorage" type="checkbox" class="infoinput_checkbox" v-model="evset.isLocalStorage"><span> localStorage</span></p>
      <p><input name="runonStart" type="checkbox" class="infoinput_checkbox" v-model="evset.runonStart"><span> v2ray</span></p>
      <p><input name="isResize" type="checkbox" class="infoinput_checkbox" v-model="evset.isResize"><span></span></p>
      <p class="tip">*  localStorage  elecV2.json <br>* </p>
    </div>
    <p class="center"><button class="nbutton nbutton--long" @click="saveBset"></button></p>
  </div>
</template>

<script>
export default {
  name: 'elecV2SetBases',
  data(){
    return {
      inbound: this.$store.state.gConf.inbound,
      log: this.$store.state.gConf.log,
      evset: this.$store.state.gConf.evset,
      gdns: this.$store.state.gConf.dns
    }
  },
  computed: {
    dns: {
      get(){
        return this.gdns.join(",")
      },
      set(newValue){
        this.gdns = newValue.split(",")
      }
    }
  },
  methods: {
    saveBset(){
      if (this.inbound.sport == this.inbound.hport) {
        this.$elecV2Alert("http  socks ")
        return false
      }
      if (this.inbound.sport && this.inbound.hport) {
        this.inbound.sport = Number(this.inbound.sport)
        this.inbound.hport = Number(this.inbound.hport)
      } else {
        this.$elecV2Alert("http  socks ")
        return false
      }
      this.evset.maxSubsers = Number(this.evset.maxSubsers) || 10
      // this.$root.gConf.dns = this.gdns
      this.$store.commit('saveGconf')
      this.$elecV2Alert("")
    }
  }
}
</script>

